<script lang="ts">
    export let width: number
    export let height: number
    export let domainY: [number, number]
    export let domainX: [number, number]
    import * as d3 from "d3";
    import { afterUpdate } from "svelte";

    let axisXContainer: SVGElement
    let axisYContainer: SVGElement

    const renderAxisX = () => {
        if (!axisXContainer) return

        const scaleX = d3.scaleLinear()
                         .domain(domainX)
                         .range([0, width])

        const axis = d3.axisBottom(scaleX)
        axis.ticks(8, ".3s")

        // @ts-ignore
        d3.select(axisXContainer).call(axis) 
    }

    const renderAxisY = () => {
        if (!axisYContainer) return

        const scaleY = d3.scaleLinear()
                         .domain(domainY)
                         .range([height, 0])        

        const axis = d3.axisLeft(scaleY)

        // @ts-ignore
        d3.select(axisYContainer).call(axis)
    }

    afterUpdate(() => {
        renderAxisX()
        renderAxisY()
    })
</script>

<g>
    <g bind:this={axisXContainer} transform={`translate(0,${height})`}/>
    <g bind:this={axisYContainer}/>
</g>
