<script lang="ts">
	import { ensureExperimentsAreLoaded, getExperimentsMetadata, getLoadedExperimentsMetadata } from '../stores/data_api_wrapper';
	import { plotterStore } from './../stores/plotter_store';
    import type { ChartType, MultipleChartsSpec, SummaryChartsForEnsemble, SingleChartSpec } from "../types/chart_spec";
    import LineLayer from "./layers/LineLayer.svelte";
    import SingleAxisLayer from "./layers/SingleAxisLayer.svelte";
    import { watchResize } from './gui-utils/watch-resize';
    import { ensureSummaryIsLoaded, getLoadedSummary } from '../stores/data_api_wrapper';

    // For summary we don't need a selector,
    // Maybe just pass a "*" or something like that
    export let axisMarginLeft: number = 35
    export let axisMarginBottom: number = 35
    export let showAxisX: boolean = true
    export let showAxisY: boolean = true
    
    export let spec: MultipleChartsSpec

    const ensureExperimentIsSelected = async (): Promise<void> => {
        await ensureExperimentsAreLoaded()
        const metadata = getLoadedExperimentsMetadata()
        if (spec.query.experiment === "auto") {
            spec.query.experiment = Object.keys(metadata)[0]
        }

        // Now also ensure the selector refers to an existing keyword
        const experiment = metadata[spec.query.experiment]
        const summaryKeys = experiment.responses.summary.keys
        
        // Assumption: Selector is always A==B format for this
        // Will be a bit more involved to make it support more
        // but it is also totally not needed at the moment
        if (!summaryKeys.includes(spec.query.keyword))
            spec.query.keyword = summaryKeys[0]
    }

    $: experimentName = () => getLoadedExperimentsMetadata()[spec.query.experiment].name

    $: computeChartStyle = (ensemble: string, ensembleAlias: string): Record<string,string> => {
        const theStyle: Record<string, string> = {}

        if (spec.style) {
            if (spec.style[ensembleAlias]) 
                Object.assign(theStyle, spec.style[ensembleAlias])
            
            if (spec.style[ensemble]) 
                Object.assign(theStyle, spec.style[ensembleAlias])
        }

        const globalStyle = $plotterStore.style
        Object.keys(globalStyle)
            .filter(k => k.startsWith("ensemble:"))
            .filter(k => k.split("ensemble:")[1] === ensembleAlias)
            .forEach(k => Object.assign(theStyle, globalStyle[k]))

        return theStyle
    }
    
    $: summary = () : SummaryChartsForEnsemble => getLoadedSummary(spec.query)

    $: performanceInfo = () => {
        const summary = getLoadedSummary(spec.query)
        const { timeSpentSeconds, MBProcessed } = summary
        return `Spent ${timeSpentSeconds.toFixed(4)} seconds to load a ${summary.data.length} realizations over ${MBProcessed}MB of files`
    }
    
    $: charts = (): SingleChartSpec[] => {
        const summary = getLoadedSummary(spec.query)
        const { style, type } = spec
        return summary.data
    }

    $: axisX = (): string => getLoadedSummary(spec.query).axisX
    $: axisY = (): string => getLoadedSummary(spec.query).axisY

    $: sharedDomainY = (): [number, number] => {
        const summary = getLoadedSummary(spec.query)
        const yDomains = summary.data.map(c => c.domainY)
        return [Math.min(...yDomains.map(d => d[0])), Math.max(...yDomains.map(d => d[1]))]
    }
    
    $: sharedDomainX = (): [number, number] => {
        const summary = getLoadedSummary(spec.query)
        const xDomains = summary.data.map(c => c.domainX)
        return [Math.min(...xDomains.map(d => d[0])), Math.max(...xDomains.map(d => d[1]))]
    }

    const typeToComponentType: Record<ChartType, any> = {
        line: LineLayer,
        area: LineLayer,
        bars: LineLayer,
        dots: LineLayer
    }


    let containerWidth = 0
    let containerHeight = 0
    function handleResize(node: HTMLElement) {
        const resizeObserver = new ResizeObserver(entries => {
            for (const entry of entries) {
                const { width, height } = entry.contentRect;
                containerWidth = width;
                containerHeight = height;
            }
        });

        resizeObserver.observe(node);

        return {
            destroy() {
                resizeObserver.disconnect();
            }
        };
    }

    const onResize = (event: CustomEvent): void => {
        const { entries } = event.detail
        const entry = entries[0]
        const { width, height } = entry.contentRect;
        containerWidth = width;
        containerHeight = height;
    }
</script>

{#await Promise.all([ensureSummaryIsLoaded(spec.query), ensureExperimentIsSelected()])}
    <p>Loading datasets... pls wait</p>
{:then}
    <div class="relative w-full h-full shadow-lg rounded m-5">
        <div class="flex flex-row items-center justify-evenly">
            <div class="p-2">
                <p>Experiment</p>
                {#if Object.keys(getLoadedExperimentsMetadata()).length > 1}
                    <select bind:value={spec.query.experiment}>
                        {#each Object.keys(getLoadedExperimentsMetadata()) as experimentId}
                            <option value={experimentId}>
                                {getLoadedExperimentsMetadata()[experimentId].name}
                            </option>
                        {/each}
                    </select>
                {:else}
                    <p>{getLoadedExperimentsMetadata()[spec.query.experiment].name}</p>
                {/if}
            </div>
            <div class="p-2">ensembles: {spec.query.ensembles}</div>
            <div class="p-2">selector: {spec.query.selector}</div>
        </div>
        <div class="p-2">{performanceInfo()}</div>
        <div   
            class="h-96 relative"
            use:watchResize
            on:resized={onResize}
            >
            {#if (containerWidth > 0 && containerHeight > 0)}
                <svg class="absolute top-0 left-0 h-full w-full overflow-visible">
                    {#if showAxisX || showAxisY}
                        <g transform={`translate(${axisMarginLeft},0)`}>
                            <SingleAxisLayer
                                width={containerWidth-axisMarginLeft}
                                height={containerHeight-axisMarginBottom}
                                domainX={sharedDomainX()}
                                domainY={sharedDomainY()}
                                axisYKey={summary().axisY}
                                axisXKey={summary().axisX}
                            />
                        </g>
                    {/if}
                    <g transform={`translate(${axisMarginLeft},0)`}>
                        {#each charts() as chart}
                            <svelte:component
                                this={typeToComponentType[spec.type]}
                                width={containerWidth-axisMarginLeft}
                                height={containerHeight-axisMarginBottom}
                                domainX={chart.domainX}
                                domainY={chart.domainY}
                                sharedDomainX={sharedDomainX()}
                                sharedDomainY={sharedDomainY()}
                                points={chart.points}
                                realization={chart.realization}
                                style={computeChartStyle(chart.ensemble, chart.ensembleAlias)}
                            />
                        {/each}
                    </g>
                </svg>
            {/if}
        </div>
    </div>
{/await}

