import type { EnsembleSummaryChartQuery, Experiments, SummaryChartsForEnsemble } from "../types/chart_spec";

const urlParams = new URLSearchParams(window.location.search);
const serverURL = decodeURIComponent(urlParams.get("serverURL") || "http://localhost:8001")

// Just for dev
const lag = async (t: number): Promise<void> => new Promise(resolve => setTimeout(resolve, t))
const minLagMs = 0

const createQueryString = (obj: EnsembleSummaryChartQuery): string => {
    const urlParams = new URLSearchParams()
    Object.entries(obj).forEach(([k, v]) => urlParams.append(k, v.toString()))
    return urlParams.toString()
}

const _summaryCache: Record<string, SummaryChartsForEnsemble> = {}
export const getSummary = async (query: EnsembleSummaryChartQuery): Promise<SummaryChartsForEnsemble> => { 
    await lag(minLagMs)
    const queryString = createQueryString(query)
    if (!(queryString in _summaryCache)) {
        const response = await fetch(`${serverURL}/api/summary_chart_data?${queryString}`)
        const responseJSON = await response.json()

        _summaryCache[queryString] = responseJSON as SummaryChartsForEnsemble
    }
    
    return _summaryCache[queryString]
}

export const ensureSummaryIsLoaded = getSummary

export const getLoadedSummary = (query: EnsembleSummaryChartQuery): SummaryChartsForEnsemble => {
    const queryString = createQueryString(query)
    return _summaryCache[queryString]
}

let _experimentsMetadataCache: Experiments|undefined = undefined
export const getExperimentsMetadata = async (forceRefresh=false): Promise<Experiments> => {
    await lag(minLagMs)
    if (!_experimentsMetadataCache || forceRefresh) {
        const response = await fetch(`${serverURL}/api/experiments`)
        _experimentsMetadataCache = await response.json() as Experiments
    }

    return _experimentsMetadataCache
}

export const ensureExperimentsAreLoaded = getExperimentsMetadata

export const getLoadedExperimentsMetadata = (): Experiments => _experimentsMetadataCache!
