<script lang="ts">
    export let width: number
    export let height: number
    export let domainY: [number, number]
    export let domainX: [number, number]
    export let sharedDomainX: [number, number]
    export let sharedDomainY: [number, number]
    export let points: [number, number][]
    export let realization: string
    export let style: Record<string, string>

    import * as d3 from "d3";

    $: compute = (): string => {
        const scaleXNormalizedToShared = d3.scaleLinear()
                         .domain([0, 1])
                         .range(sharedDomainX)

        const scaleXSharedToViewport = d3.scaleLinear()
                         .domain(sharedDomainX)
                         .range([0, width])
        
        const scaleYNormalizedToShared = d3.scaleLinear()
                         .domain([0, 1])
                         .range(sharedDomainY)

        const scaleYSharedToViewport = d3.scaleLinear()
                         .domain(sharedDomainY)
                         .range([height, 0])

        const scaleX = (x: number) => 
            scaleXSharedToViewport(scaleXNormalizedToShared(x))
        
        const scaleY = (y: number) => 
            scaleYSharedToViewport(scaleYNormalizedToShared(y))

        const lineGenerator = d3.line()
            .x((d: any) => scaleX(d[0]))
            .y((d: any) => scaleY(d[1]))

        // Note: D3 type declaration from @types/d3 is incorrect,
        // it should also accept any[]
        // @ts-ignore
        const lineDataString = lineGenerator(points);
        
        if (!lineDataString) {
            throw new Error("Failed to create line from data")
        }

        return lineDataString
    }
</script>

<g>
    <path class="fill-none" {... style } d={compute()}></path>
</g>
