declare namespace svelte.JSX {
    interface HTMLAttributes<T> {
        resized?: (event: CustomEvent<number> & { target: EventTarget & T }) => any;
    }
}

export const watchResize = (element: HTMLElement) => {
    const resizeObserver = new ResizeObserver(entries => {
        element.dispatchEvent(new CustomEvent("resized", { detail: { entries }} ))
    });
    resizeObserver.observe(element);

    return {
        destroy() {
            resizeObserver.disconnect();
        }
    };
}
